
import React, { Component } from 'react';
import { render } from 'react-dom';

import Dropdown_box from '../Dropdown_box';
import Header228 from '../header228';

import './style.css';


class HoverExample extends Component {
  constructor(props) {
    super(props);
    this.handleMouseHover = this.handleMouseHover.bind(this);
    this.state = {
      isHovering: false,
    };
  }

  handleMouseHover() {
    this.setState(this.toggleHoverState);
  }

  toggleHoverState(state) {
    return {
      isHovering: !state.isHovering,
    };
  }

  render() {
    return (
<div>
			<div className="all_line_box">

					<div className="my_under__line">
							<div class="under_header_line">
									<div class="left_under_header_line  dropp"></div>
									<div class="right_under_header_line"></div>
							</div>
					</div>

					<div className="my_under_line">
							<div class="under_line">
									<div class="left_under_line"></div>
							</div>
					</div>

			</div>




      <div className="d-flex boool" >

        <div className="hoooooool"
          onMouseEnter={this.handleMouseHover}
          onMouseLeave={this.handleMouseHover}
        >
         
        </div>
        {this.state.isHovering &&

				<Dropdown_box />
					}

      </div>

</div>
    );
  }
}

export default HoverExample ;
